<template>
    <div name="Chart2" class="chart2">
        <div class="data-area">
            <div class="clearfix">
                <div class="form-group col-xs-3 col-md-3" v-for="(item,index) in trueLength" :key="index">
                    <input class="form-control" type="number" v-model="data[index][0]" @keyup.enter="indexChange(data[index][0],index)"><input class="form-control" type="number" v-model="data[index][1]">
                </div>
            </div>
            <div class="clearfix">
                <div class="form-group col-xs-12 col-md-12">
                    <label>Length</label>
                    <input type="text" maxlength="2" v-model="length" @keyup.enter="reset">
                    <button class="btn btn-sm btn-info" v-on:click='reset'>Reset</button>
                    <button class="btn btn-sm btn-success" v-on:click='initCharts'>Paint</button>
                </div>
            </div>
        </div>
        <div class="chart-ctn" id="chartsCtn"></div>
    </div>
</template>
<script>
import eCharts from 'echarts';
import ecStat from 'echarts-stat';
var initData = function(length) {
  let array = [];
  for (let i = 0; i < length; i++) {
    array.push([i, '']);
  }
  return array;
};
var fetchData = function(data) {
  let array = [];
  for (let i = 0, l = data.length; i < l; i++) {
    if (data[i][1]) {
      array.push(data[i]);
    } else {
      break;
    }
  }
  return array;
};
export default {
  name: 'chart2',
  data() {
    return {
      data: [],
      length: 12,
      trueLength: 12
    };
  },
  methods: {
    reset: function() {
      console.log(this.length);
      this.trueLength = Number.parseInt(this.length);
      this.data = initData(this.trueLength);
    },
    indexChange: function(number, index) {
      number = Number.parseInt(number);
      this.data = this.data.map((item, i) => {
        if (i > index) {
          item[0] = number + i - index;
          return item;
        } else {
          return item;
        }
      });
    },
    initCharts: function() {
      let target = fetchData(this.data);
      var myRegression = ecStat.regression('polynomial', target, 3);
      myRegression.points.sort(function(a, b) {
        return a[0] - b[0];
      });
      // 
      var myCharts = eCharts.init(document.getElementById('chartsCtn'));
      myCharts.setOption({
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'value',
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          },
          max: 'dataMax',
          min: 'dataMin'
        },
        yAxis: {
          type: 'value',
          splitLine: {
            lineStyle: {
              type: 'dashed'
            }
          },
          max: 'dataMax',
          min: 'dataMin'
        },
        series: [
          {
            name: 'data',
            type: 'line',
            data: target
          },
          {
            name: 'standard',
            type: 'line',
            data: myRegression.points,
            markPoint: {
              itemStyle: {
                normal: {
                  color: 'transparent'
                }
              },
              label: {
                normal: {
                  show: true,
                  position: 'left',
                  formatter: myRegression.expression,
                  textStyle: {
                    color: '#333',
                    fontSize: 14
                  }
                }
              },
              data: [
                {
                  coord: myRegression.points[myRegression.points.length - 1]
                }
              ]
            }
          }
        ]
      });
    }
  },
  created: function() {
    this.data = initData(this.length);
  }
};
</script>
<style lang="scss" scoped>
.chart2 {
  display: flex;
  flex-direction: column;
  height: 100%;
  input {
    text-align: center !important;
  }
}
.chart-ctn {
  height: 480px;
  width: 100%;
}
</style>



